import React, { useContext, useEffect } from 'react'
import Input from '../../../shared/components/FormElements/Input'
import Button from '../../../shared/components/FormElements/Button'
import './Forminput.css'
import { useRef } from 'react'
import useCustomhooks from '../../../shared/hooks/form-custom-hooks'
import { useHttpClient } from '../../../shared/hooks/http-hook'
import LoadingSpinner from '../../../shared/components/UIElements/LoadingSpinner'
import ErrorModal from '../../../shared/components/UIElements/ErrorModal'
import { useNavigate, useParams } from 'react-router-dom'
import { AuthContext } from '../../../shared/context/auth-context'
import { useState } from 'react'
// import Card from '../../../shared/components/UIElements/Card'

const Forminput = () => {  
  const[loadedUsers, setLoadedUsers]= useState([])
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  const auth= useContext(AuthContext)
const navigate= useNavigate()
const placeId= useParams().pid;

const{  
  value:nameinput,
  entervalueisvalid:enternameisvalid,
  haserror:nameinputisvalid,
  valuechangehandler:namechangehandler,
  valueblurhandler:nameblurhandler,
  reset:resetname
} =useCustomhooks(val=>val.trim() !=="")

const{  
  value:titleinput,
  entervalueisvalid:entertitleisvalid,
  haserror:titleinputisvalid,
  valuechangehandler:titlechangehandler,
  valueblurhandler:titleblurhandler,  
  reset:resettitle
} =useCustomhooks(val=>val.trim() !=="")

const{  
  value:descriptioninput,
  entervalueisvalid:enterdescriptionisvalid,
  haserror:descriptioninputisvalid,
  valuechangehandler:descriptionchangehandler,
  valueblurhandler:descriptionblurhandler,  
  reset:resetdescription
} =useCustomhooks(val=>val.trim() !=="")

const{  
  value:mrpinput,
  entervalueisvalid:entermrpisvalid,
  haserror:mrpinputisvalid,
  valuechangehandler:mrpchangehandler,
  valueblurhandler:mrpblurhandler,  
  reset:resetmrp
} = useCustomhooks(val=>val.trim() !=="")


const{  
  value:priceinput,
  entervalueisvalid:enterpriceisvalid,
  haserror:priceinputisvalid,
  valuechangehandler:pricechangehandler,
  valueblurhandler:priceblurhandler,  
  reset:resetprice
} = useCustomhooks(val=>val.trim() !=="")


const nameref= useRef()
const titleref= useRef()
const descriptionref= useRef()
const mrpref= useRef()
const priceref= useRef()

let formisvalid= false
if(enternameisvalid && entertitleisvalid && enterdescriptionisvalid && entermrpisvalid && enterpriceisvalid ){
  formisvalid=true
}


useEffect(() => {
  const sendrequest = async () => {
    // setIsLoading(true);       
    try {
      const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL +`/places/${placeId}`);

     setLoadedUsers(responseData.place);
      // auth.customerlogin(responseData.customer.id)
      console.log(responseData.place)
    } catch (err) {}
  };
  sendrequest();
}, [sendRequest, placeId]);


const submithandler= async(event)=>{
  event.preventDefault();
    // console.log(nameref.current.value)
    
if(!enternameisvalid){
  return;  
}
if(!entertitleisvalid){
  return;  
}
if(!enterdescriptionisvalid){
  return;  
}
if(!entermrpisvalid){
  return;  
}
if(!enterpriceisvalid){
  return;  
}
resetname()
resettitle()
resetdescription()
resetmrp()
resetprice()


try {
  await sendRequest(
    process.env.REACT_APP_BACKEND_URL +`/places/${placeId}`,
    'PATCH',
    JSON.stringify({
      name: nameinput,
      title: titleinput,
      description: descriptioninput,
      mrp: mrpinput,
      price: priceinput
    }),
    {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + auth.token
    }
  );
  // auth.login(responseData.user.id);
  navigate("/sellerlogin")
} catch (err) {}
}


const deleteplace=async()=>{
  try {
    await sendRequest(
      process.env.REACT_APP_BACKEND_URL +`/places/${placeId}`,
      'DELETE',
      null,
      {
        Authorization: 'Bearer ' + auth.token
      }
      );
    // auth.delet()
    navigate("/sellerlogin")
  } catch (err) {}
}



  const nameinputonclasses= nameinputisvalid ? "form-control invalid" : "form-control"
  const titleinputonclasses= titleinputisvalid ? "form-control invalid" : "form-control"
  const descriptioninputonclasses= descriptioninputisvalid ? "form-control invalid" : "form-control"
  const mrpinputonclasses= mrpinputisvalid ? "form-control invalid" : "form-control"
  const priceinputonclasses= priceinputisvalid ? "form-control invalid" : "form-control"
  return (
    <>    
    
    <ErrorModal error={error} onClear={clearError} />
        {isLoading && <LoadingSpinner asOverlay />}


        <h2>Upload New Products</h2>


    <div className='form_input_div'>
    <form onSubmit={submithandler}>

      <div className={nameinputonclasses}>
      <Input
      id="NAME"
      type='text'
        ref={nameref}
        onChange={namechangehandler}
        onBlur={nameblurhandler}
        value={nameinput || loadedUsers.name}
      />
      {nameinputisvalid && <p>enter valid name</p>}
      </div>

      <div className={titleinputonclasses}>
      <Input
      id="TITLE"
      type= 'text'
        ref={titleref}
        onChange={titlechangehandler}
        onBlur={titleblurhandler}
        value={titleinput || loadedUsers.title}
        
      />
      {titleinputisvalid && <p>enter valid title</p>}
      </div>
      
      
      <div className={descriptioninputonclasses}>
      <Input
      id="DESCRIPTION"
      type='text'
        ref={descriptionref}
        onChange={descriptionchangehandler}
        onBlur={descriptionblurhandler}
        value={descriptioninput || loadedUsers.description}
      />
      {descriptioninputisvalid && <p>enter valid description</p>}
      </div>

      <div className={mrpinputonclasses}>
      <Input
      id="MRP"
      type='number'
        ref={mrpref}
        onChange={mrpchangehandler}
        onBlur={mrpblurhandler}
        value={mrpinput || loadedUsers.mrp}
      />
      {mrpinputisvalid && <p>enter valid mrp</p>}
      </div>
      
      <div className={priceinputonclasses}>
      <Input
      id="PRICE"
      type='number'
        ref={priceref}
        onChange={pricechangehandler}
        onBlur={priceblurhandler}
        value={priceinput || loadedUsers.price}
      />
      {priceinputisvalid && <p>enter valid price</p>}
      </div>
      
      <Button disabled={!formisvalid}>Update</Button>
      {/* <Button to ='/sellerlogin'>Edit</Button> */}
      <Button onClick ={deleteplace}>Delete</Button>
    </form>
    </div>
    </>
  )
}

export default Forminput
